import React from "react";

import { colors } from "../styleguide";

import { CalendarDateButtonProps, StyledSvg } from "./DateButton.styles";

type EllipseProps = Pick<CalendarDateButtonProps, "isInRange" | "selected">;


export const Ellipse = ({
	isInRange,
	selected,
}: EllipseProps) => {
	const selectedColor = selected ? colors.white : colors.black;
	const fill = isInRange ? colors.midBlue : selectedColor;

	return (
		<StyledSvg
			data-qa="date-marker-ellipses"
			width="4"
			height="4"
			viewBox="0 0 4 4"
			fill="none"
			xmlns="http://www.w3.org/2000/svg">
			<circle data-qa="circle-date-marker-ellipses" cx="2" cy="2" r="2" fill={fill} />
		</StyledSvg>
	);
};

